import { ILabShell } from '@jupyterlab/application';
import * as React from 'react';
import { ReactWidget } from '@jupyterlab/apputils';
import { codeList } from './code';
/**
 * Initialization data for the jupyterlab-sidepanel extension.
 */
const extension = {
    id: 'jupyterlab_symbol_sidebar:plugin',
    autoStart: true,
    requires: [ILabShell],
    activate: (app, shell) => {
        var checkbox = false;
        const handleChange = () => {
            checkbox = !checkbox;
            console.log(checkbox);
        };
        function MyComponent() {
            var list = codeList;
            return (React.createElement("div", { className: "sidebar-container" },
                React.createElement("div", { className: "notice" }, "Click on the icon will insert it to the cell"),
                React.createElement("label", { className: "notice" },
                    React.createElement("input", { type: "checkbox", onChange: handleChange }),
                    "Copy to clipboard"),
                React.createElement("div", { className: "block_container" }, list.map((item, i) => React.createElement("div", { key: i, onClick: () => iconClick(item.unicode) },
                    item.unicode,
                    React.createElement("div", null, item.name))))));
        }
        function iconClick(code) {
            if (checkbox)
                navigator.clipboard.writeText(JSON.parse(`["` + code + `"]`)[0].toString());
            else
                app.commands.execute('unicode:insert', { text: JSON.parse(`["` + code + `"]`)[0].toString() });
        }
        console.log('JupyterLab extension jupyterlab_symbol_sidebar is activated!');
        const newWidget = () => {
            // Create a blank content widget inside of a MainAreaWidget
            const widget = ReactWidget.create(React.createElement(MyComponent, null));
            //const widget = new MainAreaWidget({ content });
            widget.id = 'jupyterlab_symbol_sidebar';
            widget.title.label = 'Symbols';
            widget.title.closable = true;
            return widget;
        };
        let widget = newWidget();
        // let summary = document.createElement('p');
        // widget.node.appendChild(summary);
        // summary.innerText = "Hello, World!";
        shell.add(widget, 'left');
    }
};
export default extension;
